import React, { useState } from 'react'
import Countdown from 'react-countdown'
import { useVotes } from './hooks'
import { VoteModal } from './VoteModal'
import { PieChart } from 'react-minimal-pie-chart'

const countdownRenderer = ({ days, hours, minutes, seconds }) => {
  return (
    <div className='w-full text-center border-solid border-4 border-white p-4 mt-4'>
      <p className='justify-center text-white'>
        {days} jours {hours} heures <br />
        {minutes} minutes et {seconds} secondes
      </p>
    </div>
  )
}

const count = (collection, query) => {
  const key = Object.keys(query)[0]
  const value = Object.values(query)[0]
  const result = collection.filter((el) => el[key] === value)
  return result.length
}

function percentage(percent, total) {
  return ((100 * percent) / total).toFixed(2)
}

const averageDate = (votes) => {
  const dateNumber = votes.reduce((n, { birthDate }) => n + birthDate, 0)
  return new Date(dateNumber / votes.length)
}

const revealDate = '2022-03-12T11:00:00'
const diffInMs = new Date(revealDate) - new Date()
const diffInDays = Math.floor(diffInMs / (1000 * 60 * 60 * 24))
const VIDEO_BY_DAYS_LEFT = {
  0: process.env.REACT_APP_VIDEO_0,
  1: process.env.REACT_APP_VIDEO_1,
  2: process.env.REACT_APP_VIDEO_2,
  3: process.env.REACT_APP_VIDEO_3,
  4: process.env.REACT_APP_VIDEO_4,
  5: process.env.REACT_APP_VIDEO_5,
  6: process.env.REACT_APP_VIDEO_6,
  7: process.env.REACT_APP_VIDEO_7,
  8: process.env.REACT_APP_VIDEO_8,
  9: process.env.REACT_APP_VIDEO_9,
  10: process.env.REACT_APP_VIDEO_10,
  11: process.env.REACT_APP_VIDEO_11,
  12: process.env.REACT_APP_VIDEO_12,
  13: process.env.REACT_APP_VIDEO_13,
  14: process.env.REACT_APP_VIDEO_14,
  15: process.env.REACT_APP_VIDEO_15,
  16: process.env.REACT_APP_VIDEO_16,
  17: process.env.REACT_APP_VIDEO_17,
}
const videoOfTheDay = VIDEO_BY_DAYS_LEFT[diffInDays]

const App = () => {
  const [countdownCompleted, setCountdownCompleted] = useState(false)
  const [voteModalVisible, setVoteModalVisible] = useState(false)
  const [submittingVote, setSubmittingVote] = useState(false)
  const { votes, createVote } = useVotes()
  const boyVotesCount = count(votes, { gender: 'boy' })
  const girlVotesCount = count(votes, { gender: 'girl' })
  const averageWeight =
    votes.reduce((n, { weight }) => n + weight, 0) / votes.length || 0
  const averageHeight =
    votes.reduce((n, { height }) => n + height, 0) / votes.length || 0
  return (
    <div className='grid grid-cols-3 bg-gradient-to-r from-blue-200 to-pink-200 min-h-screen'>
      <div className='flex items-center justify-center'>
        <div className='text-white text-lg text-center'>
          Poids moyen : {averageWeight} kg
        </div>
      </div>
      <div className='justify-center items-center'>
        {countdownCompleted ? (
          <iframe
            className='absolute inset-0 w-full h-full m-0 p-0 overflow-hidden'
            title='secreIframe'
            allow='autoplay'
            frameBorder='0'
            seamless='seamless'
            src={process.env.REACT_APP_SECRET_URL}
          />
        ) : (
          <Countdown
            renderer={countdownRenderer}
            date={revealDate}
            onComplete={() => {
              setCountdownCompleted(true)
            }}
          />
        )}
        <div className='text-white mt-5 text-center mb-4 mb-4'>
          Si vous ne savez pas quoi faire en attendant. Vous pouvez :
          <ul className='leading-8'>
            <li>
              <a
                href={videoOfTheDay}
                target='_blank'
                rel='noreferrer'
                className='underline'
              >
                Regarder la vidéo du jour
              </a>
            </li>
            <li
              className='cursor-pointer underline'
              onClick={() => setVoteModalVisible(true)}
            >
              Faire vos pronostics
            </li>
            <li>
              <a
                className='underline'
                href='https://www.listedenaissance.fr/durant%20atkinson'
                target='_blank'
                rel='noreferrer'
              >
                Jeter un coup d'oeil à notre Liste de naissance
              </a>
            </li>
          </ul>
          <div className='flex items-center justify-center mt-8'>
            <div className='text-white text-lg text-center'>
              Date moyenne du terme :{' '}
              {averageDate(votes).toLocaleDateString('fr')}
            </div>
          </div>
          {votes.length > 0 && (
            <PieChart
              animate
              viewBoxSize={[150, 150]}
              center={[75, 75]}
              labelStyle={{
                fill: 'white',
                fontSize: '50%',
              }}
              label={(props) => {
                return props.dataEntry.title
              }}
              data={[
                {
                  title:
                    girlVotesCount > 0 &&
                    `${percentage(girlVotesCount, votes.length)} %`,
                  value: girlVotesCount,
                  color: '#fed7e2',
                },
                {
                  title:
                    boyVotesCount &&
                    `${percentage(boyVotesCount, votes.length)} %`,
                  value: boyVotesCount,
                  color: '#bfdbfe',
                },
              ]}
            />
          )}
        </div>
      </div>
      <div className='flex items-center justify-center'>
        <div className='text-white text-lg text-center'>
          Taille moyenne : {averageHeight} cm
        </div>
      </div>
      <VoteModal
        visible={voteModalVisible}
        setVisible={setVoteModalVisible}
        submitting={submittingVote}
        onSubmit={async ({
          firstName,
          lastName,
          babyGender,
          babyHeight,
          babyWeight,
          babyBirthDate,
        }) => {
          try {
            setSubmittingVote(true)
            await createVote({
              firstName,
              lastName,
              babyGender,
              babyHeight,
              babyWeight,
              babyBirthDate,
            })
          } finally {
            setSubmittingVote(false)
            setVoteModalVisible(false)
          }
        }}
      />
    </div>
  )
}

export default App
